<template>
  <div class="hello">
	<mt-header title="home" class="header">
	  <router-link to="/" slot="left">
	    <i class="iconfont icon-mulu" @click='check'></i>
	  </router-link>
	  <mt-button icon="more" slot="right"></mt-button>
	</mt-header>
    <slide :msg='hello'></slide>
  </div>
</template>

<script>
import slide from './slide.vue'
export default {
  name: 'heads', 
  components: {
    slide
  },
  data () {
    return {
       hello:false
    }
   },
   methods: {
    check () {
    		this.hello = !this.hello;
    }
  }
}
</script>
<style lang="scss" rel="stylesheet/scss">
	.icon-menu,.mintui-more{
		font-size: 0.5rem;
	}
	
</style>



