import Vue from 'vue'
import Router from 'vue-router'
import when from '@/components/views/home'
import dataplan from '@/components/views/dataplan'
import eatWhat from '@/components/views/eatWhat'
import editFood from '@/components/views/editFood'
import biji from '@/components/views/biji'
import abc from '@/components/views/abc'
import def from '@/components/views/def'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: when
    },
    {
      path: '/dataplan',
      name: 'dataplan',
      component: dataplan
    },
    {
      path: '/home',
      name: 'home',
      component: when
    },
    {
      path: '/eatWhat',
      name: 'eatWhat',
      component: eatWhat
    },
    {
      path: '/editFood',
      name: 'editFood',
      component: editFood
    },
    {
      path: '/biji',
      name: 'biji',
      component: biji
    },
    {
      path: '/abc',
      name: 'abc',
      component: abc
    },
    {
      path: '/def',
      name: 'def',
      component: def
    },
  ]
})



