<template>
<div class="editFood">
	  <div class="headerPart">
      <h1 class="myTitle">编辑食物</h1>
      </div>
      <div class="editPart">
          <input type="text" v-model="editFood" >
      </div>
      <div class="fixBottom spacing confirm slideIn dataPlan" @click="change">
        <a href="javascript:;" class="weui-btn weui-btn_primary" >确定</a>
      </div>
</div>
</template>

<script>
	export default {
  		data(){
  			return{
  				editFood:'111',
  				index:''
  			}
  		},
  		mounted(){
		      this.editFood = this.$route.params.title
		      this.index = this.$route.params.index
		},
		methods: {
			change:function(){
				this.$router.push({ name: 'eatWhat', params: { index: this.index,title:this.editFood}})
			}
		}
    }
</script>

<style  lang="scss" rel="stylesheet/scss">
.editFood{
	input{
		border: none;
	    outline: none;
	    border-bottom: 1px solid green;
	    width: 90%;
	    font-size: 0.4rem;
	    padding-bottom: 5px;
	    margin: 0 5%;
	}
	h1{
		font-size: 1rem;
	    color: rgb(16, 142, 233);
	    font-weight: 400;
	    text-align: center;
	    line-height: 5rem
	}
	.fixBottom{
		position: fixed;
	    bottom: 0px;
	    width: 100%;
	}
}
</style>


