<template>
  <div class="when" ref="target">
  	<div class="title">
  		<img src="../../assets/head.jpg" alt="" />
  	</div>
     <ul class="menu">
     	<li @click="jumpTo('dataplan')">
     		<i class="icon iconfont icon-rili" ></i><br>
     		<span>dataplan</span>
     	</li>
     	<li @click="jumpTo('eatWhat')">
     		<i class="icon iconfont icon-fan " ></i><br>
     		<span>今天吃什么</span>
     	</li>
     	<li @click="jumpTo('biji')">
     		<i class="icon iconfont icon-beiwanglu " ></i><br>
     		<span>备忘录</span>
     	</li>
     	<li @click="jumpTo('abc')">
     		<i class="icon iconfont icon-shiliangzhinengduixiang9 " ></i><br>
     		<span>什么时候放假</span>
     	</li>
     	<li @click="jumpTo('def')">
     		<i class="icon iconfont icon-yingbi" ></i><br>
     		<span>抛硬币</span>
     	</li>
     	<li>
     		<i class="icon iconfont icon-jizhang" ></i><br>
     		<span>记记账</span>
     	</li>
     	<li>
     		<i class="icon iconfont icon-pifu"></i><br>
     		<span>皮肤</span>
     	</li>
     	<li>
     		<i class="icon iconfont icon-shezhi" ></i><br>
     		<span>设置</span>
     	</li>
     </ul>
  </div>
</template>
<script>
  export default {
    data () {
      return {
        
      }
    },
    methods: {
    		jumpTo (page) {
      		this.$router.push({name: page});
    		},
    		
    }
  }
</script> 
<style lang="scss" rel="stylesheet/scss" scoped>
.when{
	text-align: center;
	.title{
		display: inline-block;
		width: 30%;
		border-radius: 50%;
		overflow: hidden;
		margin: 0.625rem 0;
		img{
		   width: 100%;
		   vertical-align: middle;
		}
	}
    ul{
    		position: relative;
    		left: 0;
    		right: 0;
    		border-left:1px solid #d9d9d9;
    		border-top:1px solid #d9d9d9;
    		box-sizing: border-box;
    		overflow: hidden;
    		li{
    			width: 33.33%;
    			list-style: none;
    			height: 3.2rem;
    			padding: 1rem 0;
    			box-sizing: border-box;
    			border-right:1px solid #d9d9d9;
    			border-bottom:1px solid #d9d9d9;
    			float: left;
    			margin: auto 0;
    		}
    }

}
</style>



