<template>
  <div class="EatWhat">
     <div class="headerPart">
        <h1 class="myTitle">今天吃什么呢</h1>
        <p class="info">从此不再纠结</p>
      </div>
      <div class="weui-grids">
        <div  v-for="(grid, index) in grids">
            <a href="javascript:;" class="weui-grid" :class="{active: grid.index === activeIndex}" 
               @click=random(grid.clickable,grid.index,grid.title)>
	            <div class="weui-grid__icon">
	                <img :src='"static/image/"+grid.image' alt="">
	            </div>
	            <p class="weui-grid__label" v-if="grid.title !== '开始'">{{grid.title}}</p>
	            <p class="weui-grid__label" v-if="grid.title === '开始'">{{doingText}}</p>
        		</a>
        </div>
    </div>
  </div>
</template>
<script>
export default {
  data () {
    return {
      grids: [
        {title: '云吞面', image: 'food1.png', clickable: false, index: 1},
        {title: 'M记', image: 'food2.png', clickable: false, index: 2},
        {title: 'K记', image: 'food3.png', clickable: false, index: 3},
        {title: '沙拉', image: 'food4.png', clickable: false, index: 8},
        {title: '开始', image: 'begin.png', clickable: true, index: 0},
        {title: '方便面', image: 'food6.png', clickable: false, index: 4},
        {title: '水果', image: 'food7.png', clickable: false, index: 7},
        {title: '串串', image: 'food8.png', clickable: false, index: 6},
        {title: '冰淇淋', image: 'food9.png', clickable: false, index: 5}
      ],
      activeIndex: 1,
      doingText: '开始',  
      doingTimer: '',
      flag: false,
      timer: '',
      finish: 1,
      editIndex: '',
      editFood:''
    }
  },
  watch:{
  	 editIndex : function(){
  	 	if(this.editIndex){
      		this.grids[this.editIndex-1].title = this.editFood
      }
  	 }
  },
  methods: {
    random (con, index,title) {
    		if(index !== 0){
    			this.$router.push({ name: 'editFood', params: { title: title,index:index}})
    		}
    		this.startTime = new Date()
        this.doingText = '纠结中'
        this.loadingText()
        var timer = setInterval(this.add, 100)				//前期加速
        setTimeout(() => {
          clearInterval(timer)
          this.finish = 1
          this.flag = true									//是否释放
          this.utimal = 9 + Math.round(Math.random() * 8)	//随机次数
          this.timer = setInterval(this.add, 200)
        }, 1000)
    },
    loadingText () {
      this.doingTimer = setInterval(() => {
        this.doingText = this.doingText.length >= 6 ? this.doingText.replace(/\./g, '') : this.doingText + '.'
      }, 500)
    },
    add () {
      if (this.activeIndex === 8) {
        this.activeIndex = 1
      } else {
        this.activeIndex++
      }
      if (this.flag) {
        if (this.finish === this.utimal) {
          clearInterval(this.timer)
          clearInterval(this.doingTimer)
          this.flag = false
          this.doingText = '开始'
          this.endTime = new Date()
          console.log(this.endTime-this.startTime)
        } else {
          this.finish++
        }
      }
    }
  },
  mounted(){
      this.editFood = this.$route.params.title
      this.editIndex = this.$route.params.index
      this.$axios.get('../../../static/eatFoods.json')
        .then((data) => {
          console.log(data)
        })
  	},
}
</script>

<style lang="scss" rel="stylesheet/scss">
    .EatWhat {
        .headerPart {
            padding-bottom: 1rem;
            text-align: center;
            .myTitle {
                color: cornflowerblue;
                font-size: 0.8rem;
                font-weight: 400;
            }
            .info {
                color: gray;
                font-size: 0.6rem;
            }
        }
        .active {
            background: #E8C2A8;
        }
    }
</style>



