<template>

	<div class="slide togglehide"   @click='toggleShow' ref='barPart'>
		<div class='menuCover'ref='cover'></div>
		
		<ul class="items">
			<li>
				<i class='iconfont icon-yingbi'></i>
				<router-link to="/">首页</router-link>
			</li>
		</ul>
	</div>

</template>

<script>
	export default {
  		name: 'slide',
  		props : {
	      msg: {
		      type: Boolean,
		      default: false
		    }
	   },
  		watch: {
		   msg:"toggleShow"
		},
		methods: {
		    toggleShow () {
		    		 let target = this.$refs.barPart
      			 let test = target.classList.contains('togglehide')
		    		 if (!test) {
		    		 	target.classList.add('togglehide')
		    		 }else{
		    		 	target.classList.remove('togglehide')
		    		 }
		    },
		    change(){
		    		this.toggleShow ()
		    		console.log(999)
		    }
		}
 	}
</script>

<style lang="scss" rel="stylesheet/scss">
	
	.slide{
		position: fixed;
		top: 0;
		bottom: 0;
		background: #2c3b41;
		width: 60%;
		z-index: 99;
		.menuCover {
		    position: fixed;
		    left: 0px;
		    right: 0px;
		    top: 0px;
		    bottom: 0px;
		    background: #2c3b41;
		    opacity: .5;
		    z-index: 1111；
		  }
		.items{
			position: relative;
			z-index: 1112;
			margin-top: 60px;
			
			li{
				color: #fff;
				float: left;
				a{
					color: #fff;
				}
			}
		}
	}
	.togglehide{
		display: none;
	}

</style>


