<template>
  <div class="dataplan" ref='target'>
     <h1>今天做点什么</h1>
     <p class="uptext">
     	<input type="text" placeholder="好好学习，天天向上" class="textPart" v-model="newTask"/>
     	<i class="icon iconfont icon-addition" @click="add()"></i>
     </p>
     
     <div class="todo">
     	<p>TODOLIST</p>
     	<ul>
     		<li v-for="(task, index) in todoTask">
     			<i class="icon iconfont icon-yuan" @click="setDone(index)"></i>
     			<input class="tre" type="text" :value="task" disabled @blur="change(index)"/>
     			<i class="icon iconfont icon-jilu" @click="editTodo(index)"></i>
     		</li>
     	</ul>
     </div>
     <div class="done">
     	<p>DONELIST</p>
     	<ul>
     		<li v-for="(task, index) in doneTask">
     			<i class="icon iconfont icon-success_fill" style="color: green;"></i>
     			<input type="text" :value="task" disabled/>
     			<i class="icon iconfont icon-delete" @click="deletedo(index)" style="color: red;"></i>
     		</li>
     	</ul>
     </div>
     
     <mt-button type="primary" size="large" @click="actionSheet">确认操作</mt-button> 
     <mt-actionsheet  
        :actions= "data"  
        v-model="sheetVisible">  
    </mt-actionsheet>  
  </div>
</template>
<script>
export default {
  data () {
    return {
    		newTask:'',
    		edit:'',
    		todoTask: [], 
      	doneTask: [], 
        data: [{  
        		name: '删除所有列表',  
        		method : this.resetsAll
      	}, {  
        		name: '删除todo列表',   
        		method : this.resetsTodo
      	}, {  
        		name: '删除done列表',   
        		method : this.resetsDone
      	}],  
       	sheetVisible: false  
    }
  },
  watch: {
    
  },
  created () {
  	
  },
  methods: {
  	
  	change: function(index){
  		this.todoTask[index] = this.$refs.target.getElementsByClassName("tre")[index].value;
  		this.$refs.target.getElementsByClassName("tre")[index].setAttribute("disabled","true");
  		this.$refs.target.getElementsByClassName("tre")[index].style.border = 'none'
  	},
  	add: function(){
  		this.todoTask.push(this.newTask)
  		this.newTask=""
  	},
  	setDone: function(index){
  		this.doneTask.push(this.todoTask[index])
  		this.todoTask.splice(index, 1)
  	},
  	deletedo: function(index){
  		this.doneTask.splice(index, 1)
  	},
  	editTodo: function(index){
  		this.$refs.target.getElementsByClassName("tre")[index].removeAttribute("disabled");
  		this.$refs.target.getElementsByClassName("tre")[index].focus();
  		this.$refs.target.getElementsByClassName("tre")[index].style.border = '1px solid orange'
  	},
    	actionSheet: function(){  
        this.sheetVisible = true;  
    },  
    resetsAll: function(){  
      	this.todoTask=[];
      	this.doneTask=[];
    },
    resetsTodo: function(){  
      	this.todoTask=[];
    },
    resetsDone: function(){  
      	this.doneTask=[];
    }
  }
}
</script>
<style lang="scss" rel="stylesheet/scss">
.dataplan{
	h1{
		font-size: 0.7rem;
		color: #26A2FF;
		line-height: 2rem;
		text-align: center;
	}
	.uptext{
		text-align: left;
		margin: 0 0.6rem;
		overflow: hidden;
		input{
			width: 88%;
			line-height: 0.6125rem;
			border: 0;
			border-bottom: 1px solid #2C3E50;
			float: left;
		}
		i{
			float: right;
    			font-size: 0.7rem;
    			line-height: 0.8rem
		}
	}
	div{
		color: #999;
		input{
    			width: 80%;
		}
	}
	div>p{
		padding: 0.6rem 0 0 0.6rem;
		line-height: 1rem;
	}
	div>ul{
    		border-top: 1px solid #e5e5e5;
    		li{
			border-bottom: 1px solid #e5e5e5;
	    		padding: 0 0.6rem;
	    		line-height: 1rem;
		}
	}
	
	button{
		position: absolute;
		bottom: 0;
	}
}  
</style>


