<template>
	<div>
	<div @click="jumpTo('def')">哈哈</div>
	<div @click="change()">来玩</div>
	 <ul>
            <li v-for="tab in tabs" >
                {{ tab.text }}
            </li>
        </ul>
	</div>
</template>

<script>
import {vm} from '../../utils/bus.js'
export default {
    data () {
      return {
        highlight:'组件A',
        item:'11',
        date:'111',
      	tabs: [
                    { text: '巴士' },
                    { text: '快车' },
                    { text: '专车' },
                    { text: '顺风车' },
                    { text: '出租车' },
                    { text: '代驾' }
                ]
      }
    },
    methods: {
    		jumpTo (page) {
      		this.$router.push({name: page});
    		},
    		change (){
    			this.tabs =  this.tabs.slice(0,2).concat({ text: '飞机' }, this.tabs.slice(2))
    		}
    },
	
}

</script>

<style>
</style>


