<template>
	<div ref="editor">
        <p>欢迎使用 <b>wangEditor</b> 富文本编辑器</p>
    </div>
</template>

<script>
	import E from '../../../static/wangEditor.min.js'
	export default {
      name: 'biji',
      data () {
        return {
          editorContent: ''
        }
      },
      methods: {
        getContent: function () {
            alert(this.editorContent)
        }
      },
      mounted() {
        var editor = new E(this.$refs.editor)
        editor.customConfig.uploadImgShowBase64 = true   // 使用 base64 保存图片
    		editor.create()
      }
    }
</script>

<style>
	
</style>


