<template>
	<div>嘻嘻</div>
</template>

<script>
import {vm} from '../../utils/bus.js'
export default {
    data () {
      return {
        
      }
    },	
	beforeCreate () {
   console.group('%c%s', 'color:blue', 'beforeCreate 创建前状态===============组件2》')
 },
 created () {
   console.group('%c%s', 'color:blue', 'created 创建完毕状态===============组件2》')
 },
 beforeMount () {
   console.group('%c%s', 'color:blue', 'beforeMount 挂载前状态===============组件2》')
 },
 mounted () {
   console.group('%c%s', 'color:blue', 'mounted 挂载状态===============组件2》')
 },
 beforeUpdate () {
   console.group('%c%s', 'color:blue', 'beforeUpdate 更新前状态===============组件2》')
 },
 updated () {
   console.group('%c%s', 'color:blue', 'updated 更新状态===============组件2》')
 },
 beforeDestroy () {
   console.group('%c%s', 'color:blue', 'beforeDestroy 破前状态===============组件2》')
 },
 destroyed () {
   console.group('%c%s', 'color:blue', 'destroyed 破坏状态===============组件2》')
 }

}
	
</script>

<style>
</style>


